"use client";

import { useState } from "react";
import Image from "next/image";
import Link from "next/link";

type AuthMode = "login" | "register" | "forgot-password";

export default function LoginPage() {
    const [mode, setMode] = useState<AuthMode>("login");
    const [rememberMe, setRememberMe] = useState(false);
    const [acceptTerms, setAcceptTerms] = useState(false);

    return (
        <div className="flex min-h-screen">
            {/* Left side - Branding/Hero */}
            <div className="hidden lg:flex lg:w-1/2 bg-gradient-to-br from-dermcare to-dermcare-dark p-12 pl-20 items-center justify-center relative overflow-hidden">
                <div className="absolute inset-0 bg-[radial-gradient(circle_at_top_right,_rgba(255,255,255,0.15),_transparent_60%)]" />
                <div className="relative z-10 text-white space-y-6 max-w-md">
                    <Link href="/" className="inline-block">
                        <Image
                            src="/kma_logo.jpg"
                            alt="KMA - Academy of Cryptography Techniques"
                            width={200}
                            height={200}
                            className="h-32 w-32 rounded-full"
                        />
                    </Link>
                    <h1 className="text-4xl font-bold">
                        Chăm sóc da liễu <br />
                        chuyên nghiệp trực tuyến
                    </h1>
                    <p className="text-dermcare-light text-lg">
                        Kết nối với bác sĩ da liễu hàng đầu, theo dõi liệu trình điều trị và
                        quản lý hồ sơ sức khỏe da liễu của bạn.
                    </p>
                    <div className="flex items-center gap-8 pt-6">
                        <div>
                            <p className="text-3xl font-bold">12.5K+</p>
                            <p className="text-dermcare-light text-sm">Ca khám</p>
                        </div>
                        <div>
                            <p className="text-3xl font-bold">4.9/5.0</p>
                            <p className="text-dermcare-light text-sm">Đánh giá</p>
                        </div>
                        <div>
                            <p className="text-3xl font-bold">&lt;5 phút</p>
                            <p className="text-dermcare-light text-sm">Chờ trả lời</p>
                        </div>
                    </div>
                </div>
            </div>

            {/* Right side - Auth Forms */}
            <div className="flex-1 flex items-center justify-center p-6 bg-slate-50">
                <div className="w-full max-w-md">
                    {/* Mobile Logo */}
                    <div className="lg:hidden mb-8 text-center">
                        <Link href="/" className="inline-block">
                            <Image
                                src="/logo_dermcare.jpg"
                                alt="Dermcare"
                                width={650}
                                height={180}
                                className="h-16 w-auto mx-auto"
                            />
                        </Link>
                    </div>

                    <div className="bg-white rounded-2xl shadow-soft border border-slate-100 p-8">
                        {/* Login Form */}
                        {mode === "login" && (
                            <div className="space-y-6">
                                <div className="space-y-2 text-center">
                                    <h2 className="text-2xl font-bold text-slate-900">
                                        Đăng nhập
                                    </h2>
                                    <p className="text-sm text-slate-600">
                                        Chào mừng bạn quay trở lại với Dermcare
                                    </p>
                                </div>

                                <div className="space-y-4">
                                    <div>
                                        <label
                                            htmlFor="email"
                                            className="block text-sm font-medium text-slate-700 mb-1.5"
                                        >
                                            Email
                                        </label>
                                        <input
                                            id="email"
                                            type="email"
                                            placeholder="yourname@example.com"
                                            className="w-full px-4 py-2.5 rounded-lg border border-slate-200 focus:border-dermcare focus:ring-2 focus:ring-dermcare/20 outline-none transition"
                                        />
                                    </div>

                                    <div>
                                        <label
                                            htmlFor="password"
                                            className="block text-sm font-medium text-slate-700 mb-1.5"
                                        >
                                            Mật khẩu
                                        </label>
                                        <input
                                            id="password"
                                            type="password"
                                            placeholder="••••••••"
                                            className="w-full px-4 py-2.5 rounded-lg border border-slate-200 focus:border-dermcare focus:ring-2 focus:ring-dermcare/20 outline-none transition"
                                        />
                                    </div>

                                    <div className="flex items-center justify-between">
                                        <label className="flex items-center gap-2 cursor-pointer">
                                            <input
                                                type="checkbox"
                                                checked={rememberMe}
                                                onChange={(e) => setRememberMe(e.target.checked)}
                                                className="w-4 h-4 rounded border-slate-300 text-dermcare focus:ring-dermcare"
                                            />
                                            <span className="text-sm text-slate-600">
                                                Nhớ mật khẩu
                                            </span>
                                        </label>
                                        <button
                                            onClick={() => setMode("forgot-password")}
                                            className="text-sm text-dermcare hover:text-dermcare-dark font-medium"
                                        >
                                            Quên mật khẩu?
                                        </button>
                                    </div>

                                    <button className="w-full bg-dermcare hover:bg-dermcare-dark text-white font-semibold py-2.5 rounded-lg transition shadow-soft">
                                        Đăng nhập
                                    </button>
                                </div>

                                <div className="relative">
                                    <div className="absolute inset-0 flex items-center">
                                        <div className="w-full border-t border-slate-200" />
                                    </div>
                                    <div className="relative flex justify-center text-sm">
                                        <span className="px-4 bg-white text-slate-500">hoặc</span>
                                    </div>
                                </div>

                                <div className="grid grid-cols-2 gap-3">
                                    <button className="flex items-center justify-center gap-2 px-4 py-2.5 border border-slate-200 rounded-lg hover:bg-slate-50 transition">
                                        <svg className="w-5 h-5" viewBox="0 0 24 24">
                                            <path
                                                fill="#1877F2"
                                                d="M24 12.073c0-6.627-5.373-12-12-12s-12 5.373-12 12c0 5.99 4.388 10.954 10.125 11.854v-8.385H7.078v-3.47h3.047V9.43c0-3.007 1.792-4.669 4.533-4.669 1.312 0 2.686.235 2.686.235v2.953H15.83c-1.491 0-1.956.925-1.956 1.874v2.25h3.328l-.532 3.47h-2.796v8.385C19.612 23.027 24 18.062 24 12.073z"
                                            />
                                        </svg>
                                        <span className="text-sm font-medium text-slate-700">
                                            Facebook
                                        </span>
                                    </button>

                                    <button className="flex items-center justify-center gap-2 px-4 py-2.5 border border-slate-200 rounded-lg hover:bg-slate-50 transition">
                                        <svg className="w-5 h-5" viewBox="0 0 24 24">
                                            <path
                                                fill="#4285F4"
                                                d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
                                            />
                                            <path
                                                fill="#34A853"
                                                d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
                                            />
                                            <path
                                                fill="#FBBC05"
                                                d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
                                            />
                                            <path
                                                fill="#EA4335"
                                                d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
                                            />
                                        </svg>
                                        <span className="text-sm font-medium text-slate-700">
                                            Google
                                        </span>
                                    </button>
                                </div>

                                <p className="text-center text-sm text-slate-600">
                                    Chưa có tài khoản?{" "}
                                    <button
                                        onClick={() => setMode("register")}
                                        className="text-dermcare hover:text-dermcare-dark font-medium"
                                    >
                                        Đăng ký ngay
                                    </button>
                                </p>
                            </div>
                        )}

                        {/* Register Form */}
                        {mode === "register" && (
                            <div className="space-y-6">
                                <div className="space-y-2 text-center">
                                    <h2 className="text-2xl font-bold text-slate-900">
                                        Đăng ký tài khoản
                                    </h2>
                                    <p className="text-sm text-slate-600">
                                        Tạo tài khoản để bắt đầu chăm sóc da liễu
                                    </p>
                                </div>

                                <div className="space-y-4">
                                    <div>
                                        <label
                                            htmlFor="fullname"
                                            className="block text-sm font-medium text-slate-700 mb-1.5"
                                        >
                                            Họ và tên
                                        </label>
                                        <input
                                            id="fullname"
                                            type="text"
                                            placeholder="Nguyễn Văn A"
                                            className="w-full px-4 py-2.5 rounded-lg border border-slate-200 focus:border-dermcare focus:ring-2 focus:ring-dermcare/20 outline-none transition"
                                        />
                                    </div>

                                    <div>
                                        <label
                                            htmlFor="reg-email"
                                            className="block text-sm font-medium text-slate-700 mb-1.5"
                                        >
                                            Email
                                        </label>
                                        <input
                                            id="reg-email"
                                            type="email"
                                            placeholder="yourname@example.com"
                                            className="w-full px-4 py-2.5 rounded-lg border border-slate-200 focus:border-dermcare focus:ring-2 focus:ring-dermcare/20 outline-none transition"
                                        />
                                    </div>

                                    <div>
                                        <label
                                            htmlFor="reg-password"
                                            className="block text-sm font-medium text-slate-700 mb-1.5"
                                        >
                                            Mật khẩu
                                        </label>
                                        <input
                                            id="reg-password"
                                            type="password"
                                            placeholder="••••••••"
                                            className="w-full px-4 py-2.5 rounded-lg border border-slate-200 focus:border-dermcare focus:ring-2 focus:ring-dermcare/20 outline-none transition"
                                        />
                                    </div>

                                    <div>
                                        <label
                                            htmlFor="confirm-password"
                                            className="block text-sm font-medium text-slate-700 mb-1.5"
                                        >
                                            Xác nhận mật khẩu
                                        </label>
                                        <input
                                            id="confirm-password"
                                            type="password"
                                            placeholder="••••••••"
                                            className="w-full px-4 py-2.5 rounded-lg border border-slate-200 focus:border-dermcare focus:ring-2 focus:ring-dermcare/20 outline-none transition"
                                        />
                                    </div>

                                    <label className="flex items-start gap-2 cursor-pointer">
                                        <input
                                            type="checkbox"
                                            checked={acceptTerms}
                                            onChange={(e) => setAcceptTerms(e.target.checked)}
                                            className="w-4 h-4 mt-0.5 rounded border-slate-300 text-dermcare focus:ring-dermcare"
                                        />
                                        <span className="text-sm text-slate-600">
                                            Tôi đồng ý với{" "}
                                            <a href="#" className="text-dermcare hover:underline">
                                                Điều khoản dịch vụ
                                            </a>{" "}
                                            và{" "}
                                            <a href="#" className="text-dermcare hover:underline">
                                                Chính sách bảo mật
                                            </a>
                                        </span>
                                    </label>

                                    <button
                                        disabled={!acceptTerms}
                                        className="w-full bg-dermcare hover:bg-dermcare-dark text-white font-semibold py-2.5 rounded-lg transition shadow-soft disabled:opacity-50 disabled:cursor-not-allowed"
                                    >
                                        Đăng ký
                                    </button>
                                </div>

                                <div className="relative">
                                    <div className="absolute inset-0 flex items-center">
                                        <div className="w-full border-t border-slate-200" />
                                    </div>
                                    <div className="relative flex justify-center text-sm">
                                        <span className="px-4 bg-white text-slate-500">hoặc</span>
                                    </div>
                                </div>

                                <div className="grid grid-cols-2 gap-3">
                                    <button className="flex items-center justify-center gap-2 px-4 py-2.5 border border-slate-200 rounded-lg hover:bg-slate-50 transition">
                                        <svg className="w-5 h-5" viewBox="0 0 24 24">
                                            <path
                                                fill="#1877F2"
                                                d="M24 12.073c0-6.627-5.373-12-12-12s-12 5.373-12 12c0 5.99 4.388 10.954 10.125 11.854v-8.385H7.078v-3.47h3.047V9.43c0-3.007 1.792-4.669 4.533-4.669 1.312 0 2.686.235 2.686.235v2.953H15.83c-1.491 0-1.956.925-1.956 1.874v2.25h3.328l-.532 3.47h-2.796v8.385C19.612 23.027 24 18.062 24 12.073z"
                                            />
                                        </svg>
                                        <span className="text-sm font-medium text-slate-700">
                                            Facebook
                                        </span>
                                    </button>

                                    <button className="flex items-center justify-center gap-2 px-4 py-2.5 border border-slate-200 rounded-lg hover:bg-slate-50 transition">
                                        <svg className="w-5 h-5" viewBox="0 0 24 24">
                                            <path
                                                fill="#4285F4"
                                                d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
                                            />
                                            <path
                                                fill="#34A853"
                                                d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
                                            />
                                            <path
                                                fill="#FBBC05"
                                                d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
                                            />
                                            <path
                                                fill="#EA4335"
                                                d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
                                            />
                                        </svg>
                                        <span className="text-sm font-medium text-slate-700">
                                            Google
                                        </span>
                                    </button>
                                </div>

                                <p className="text-center text-sm text-slate-600">
                                    Đã có tài khoản?{" "}
                                    <button
                                        onClick={() => setMode("login")}
                                        className="text-dermcare hover:text-dermcare-dark font-medium"
                                    >
                                        Đăng nhập
                                    </button>
                                </p>
                            </div>
                        )}

                        {/* Forgot Password Form */}
                        {mode === "forgot-password" && (
                            <div className="space-y-6">
                                <div className="space-y-2 text-center">
                                    <h2 className="text-2xl font-bold text-slate-900">
                                        Quên mật khẩu?
                                    </h2>
                                    <p className="text-sm text-slate-600">
                                        Nhập email của bạn để nhận link khôi phục mật khẩu
                                    </p>
                                </div>

                                <div className="space-y-4">
                                    <div>
                                        <label
                                            htmlFor="forgot-email"
                                            className="block text-sm font-medium text-slate-700 mb-1.5"
                                        >
                                            Email
                                        </label>
                                        <input
                                            id="forgot-email"
                                            type="email"
                                            placeholder="yourname@example.com"
                                            className="w-full px-4 py-2.5 rounded-lg border border-slate-200 focus:border-dermcare focus:ring-2 focus:ring-dermcare/20 outline-none transition"
                                        />
                                    </div>

                                    <button className="w-full bg-dermcare hover:bg-dermcare-dark text-white font-semibold py-2.5 rounded-lg transition shadow-soft">
                                        Gửi link khôi phục
                                    </button>
                                </div>

                                <p className="text-center text-sm text-slate-600">
                                    <button
                                        onClick={() => setMode("login")}
                                        className="text-dermcare hover:text-dermcare-dark font-medium"
                                    >
                                        ← Quay lại đăng nhập
                                    </button>
                                </p>
                            </div>
                        )}
                    </div>

                    <p className="text-center text-xs text-slate-500 mt-6">
                        © {new Date().getFullYear()} Dermcare. Bảo mật thông tin bệnh nhân
                        tuyệt đối.
                    </p>
                </div>
            </div>
        </div>
    );
}
